.buttons-div-container{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 46vmax;
    height: 4vmax;
    margin: 2vmax 2vmax 0 0;
}

.animation-reset-button{
    position: absolute;
    top: 1rem;
    right: 1rem;
    background-color: #32bafa;
    color : #fff;
    border: 0;
    border-radius: 4;
    font-size: 1rem;
    padding: 0.7rem 1.2rem;
    cursor: pointer;
    outline: none;
}


.particle-effect-button{
    background-color: #121019;
    color: #fff;
    font-size: 1.2rem;
    border: 0;
    padding: 1.5rem 3rem;
    border-radius: 5;
    cursor: pointer;
}


